@font-face {
  font-family: 'Pretendard-Regular';
  src: url('https://fastly.jsdelivr.net/gh/Project-Noonnu/noonfonts_2107@1.1/Pretendard-Regular.woff') format('woff');
  font-weight: 400;
  font-style: normal;
}

body {
  font-family: 'Pretendard-Regular';
  color: #333; font-size: 16px; font-weight: 400; line-height: 1.4;
}
a {
  color: inherit; text-decoration: none;
}
img {
  display: block;
}

/* common css */
.inner {
  width: 1100px; margin: 0 auto; position: relative;
}
/* 버튼 스타일 */
.btn {
  width: 130px; padding: 10px; border: 2px solid #333; border-radius: 4px; font-size: 16px; color: #333; font-weight: 700; text-align: center; cursor: pointer; box-sizing: border-box; display: block; transition: .4s;
}
.btn:hover {
  background-color: #333; color: #fff;
}
/* 동작에 따른 버튼 스타일 */
.btn.btn--green {
  color: #006633; border-color: #006633;
}
.btn.btn--green:hover {
  color: #fff; background-color: #006633;
}
.btn.btn--reverse {
  background-color: #006633; color: #fff; border-color: #006633;
}
.btn.btn--reverse:hover {
  background-color: transparent; color: #006633; border-color: #006633;
}
.btn.btn--brown {
  color: #a52a2a; border-color: #a52a2a;
}
.btn.btn--brown:hover {
  color: #fff; background-color: #a52a2a;
}
.btn.btn--gold {
  color: #dbaa84; border-color: #dbaa84;
}
.btn.btn--gold:hover {
  color: #fff; background-color: #dbaa84;
}
.btn.btn--white {
  color: #fff; border-color: #fff;
}
.btn.btn--white:hover {
  color: #333; background-color: #fff;
}

/* scrollMagic css */
.back-to-position {
  opacity: 0; transition: 1s;
}
.back-to-position.to-right {
  transform: translateX(-150px);
}
.back-to-position.to-left {
  transform: translateX(150px);
}
.show .back-to-position {
  opacity: 1; transform: translateX(0);
}
/* 각 요소 시간차 */
.show .back-to-position.delay-0 {transition-delay: 0s;}
.show .back-to-position.delay-1 {transition-delay: .3s;}
.show .back-to-position.delay-2 {transition-delay: .6s;}
.show .back-to-position.delay-3 {transition-delay: .9s;}

/* header */
header {
  background-color: #f6f5f0;
  border-bottom: 1px solid #c8c8c8;
  position: fixed; top: 0; width: 100%; z-index: 9;
}
header .inner {
  height: 120px;
}
header a.logo {
  height: 75px; 
  position: absolute; top: 0; bottom: 0; left: 0; margin: auto;
}
header div.sub-menu {
  display: flex; position: absolute; top: 10px; right: 0;
}
header .sub-menu .menu {
  display: flex; font-family: Arial, Helvetica, sans-serif;
}
header .sub-menu .menu li {
  position: relative;
}
header .sub-menu .menu li:before {
  content: "";
  width: 1px; height: 12px; background-color: #e5e5e5; position: absolute; top: 0; bottom: 0; margin: auto;
}
header .sub-menu .menu li:first-child::before {
  display: none;
}
header .sub-menu .menu li a {
  font-size: 12px; padding: 11px 16px; display: block; color: #656565;
}
header .sub-menu .menu li a:hover {
  color: #000;  text-decoration: underline;
}
header .sub-menu .search {
  height: 34px; position: relative;
}
header .sub-menu .search input {
  width: 36px; height: 34px; padding: 4px 10px; border: 1px solid #ccc; border-radius: 5px; outline: none; background-color: #fff; box-sizing: border-box; color: #777; font-size: 12px; transition: width .4s;
}
header .sub-menu .search input:focus {
  width: 190px; border-color: #669900;
}
header .sub-menu .search .material-icons {
  height: 24px; position: absolute; top: 0; bottom: 0; right: 5px; margin: auto; transition: .4s;
}
header .sub-menu .search.focused .material-icons {
  opacity: 0;
}

header .main-menu {
  position: absolute; bottom: 0; right: 0; z-index: 1; display: flex;
}
header .main-menu .item {
  cursor: pointer;
}
header .main-menu .item .item__name {
  display: block;
  padding: 10px 20px 20px 20px;
  font-family: Arial, Helvetica, sans-serif; font-size: 13px;
}
header .main-menu .item:hover .item__name {
  background: url(../images/main_menu_pattern.jpg); color: #669900; border-radius: 6px 6px 0 0; text-decoration: underline;
}

header .main-menu .item .item__contents {
  width: 100%; position: fixed; left: 0; 
  background-color: #ccc; display: none;
}
header .main-menu .item:hover .item__contents {
  display: block;
}
header .main-menu .item .item__contents .contents__menu {
  background: url(../images/main_menu_pattern.jpg);
}
header .main-menu .item .item__contents .contents__menu > ul {
  display: flex; padding: 20px 0; height: 100%;
}
header .main-menu .item .item__contents .contents__menu > ul > li {
  width: 220px;
}
header .main-menu .item .item__contents .contents__menu > ul > li span {
  display: block; padding: 3px 0 12px 0; font-size: 14px; color: #fff;
}
header .main-menu .item .item__contents .contents__menu > ul > li span:hover {
  color: #669900; text-decoration: underline;
}
header .main-menu .item .item__contents .contents__menu > ul > li ul li {
  padding: 5px 0; font-size: 12px; color: #999; cursor: pointer;
}
header .main-menu .item .item__contents .contents__menu > ul > li ul li:hover {
  color: #669900;
}



header .badges {
  position: fixed; top: 132px; right: 12px;
}
header .badges .badge {
  border-radius: 10px; overflow: hidden; margin-bottom: 12px; box-shadow: 4px 4px 10px rgba(0,0,0,.15); cursor: pointer;
}

/* footer */
footer {
  background-color: #272727; border-top: 1px solid #333;
}
footer .inner {
  padding: 40px 0 60px 0;
}
footer .menu {
  display: flex; justify-content: center;
}
footer .menu li {
  position: relative;
}
footer .menu li:before {
  content: ""; position: absolute; width: 3px; height: 3px; background-color: #555; top: 0; bottom: 0; margin: auto; right: -1px;
}
footer .menu li:last-child:before {
  display: none;
}
footer .menu li a {
  color: #ccc; font-size: 12px; font-weight: 700; padding: 15px; display: block;
}
footer .menu li .green {
  color: #669900;
}
footer .btn-group {
  margin-top: 20px; display: flex; justify-content: center;
}
footer .btn-group .btn {
  font-size: 12px; margin-right: 10px;
}
footer .btn-group .btn:last-child {
  margin-right: 0;
}
footer .info {
  margin-top: 30px; text-align: center;
}
footer .info span {
  margin-right: 20px; color: #999; font-size: 12px;
}
footer .info span:last-child {
  margin-right: 0;
}

footer .copyright {
  color: #999; font-size: 12px; text-align: center; margin-top: 12px;
}
footer .copyright .logo {
  margin: 30px auto;
}
footer .copyright .this-year {
  margin: 0 5px;
}

/* to-top */
.to-top {
  width: 42px; height: 42px; background-color: #333; color: #fff; border: 2px solid #fff; border-radius: 10px; cursor: pointer; display: flex; justify-content: center; align-items: center; position: fixed; bottom: 30px; right: 30px; z-index: 9;
}